import React, { useReducer } from 'react'
import UniContext from './uniContext'
import UniReducer from './UniReducer'
import {
    SEARCH_UNIS,
    CLEAR_UNIS,
    SET_LOADING,
    SET_ERROR
} from '../types'
import uniData from './uniData.json'

const UniState = (props) => {

    const initialState = {
        unis: [],
        loading: false,
        error: false
    }

    const [state, dispatch] = useReducer(UniReducer, initialState);

    // Search Universities
    const searchUnis = (name) => {

        setLoading();
        const res = uniData.filter(uni => uni.name.toLowerCase().includes(name));

        if (res.length === 0) {
            setError(true);
        } else {
            setError(false);
        }

        dispatch({
            type: SEARCH_UNIS,
            payload: res.slice(0, 100)
        });
    }

    // Clear Universities
    const clearUnis = () => {
        dispatch({
            type: CLEAR_UNIS
        });
    }

    // Set Loading
    const setLoading = () => {
        dispatch({
            type: SET_LOADING
        });
    }

    // Set Error
    const setError = (bool) => {
        dispatch({
            type: SET_ERROR,
            payload: bool
        });
    }

    return (
        <UniContext.Provider
            value={{
                unis: state.unis,
                loading: state.loading,
                error: state.error,
                searchUnis,
                clearUnis,
                setError
            }}
        >
            { props.children}
        </UniContext.Provider>
    )
}

export default UniState;